import { Link } from "react-router-dom";

const RandomCatchItem = (props) => {
    return (
        <div className='random-catch-item-container flex'>
            {console.log(props.fishData[6]["Image Gallery"])}
            { props.randomCatch.map((fish) => (
                <div className='random-catch-item' key={fish}>
                    <Link to={'fish/' + fish + '/' + props.fishData[fish]['Species Name']}><h6>{ props.fishData[fish]['Species Name'] }</h6></Link>
                    <Link to={'fish/' + fish + '/' + props.fishData[fish]['Species Name']}>
                        {props.fishData[fish]['Image Gallery'][0] && <img src={ props.fishData[fish]['Image Gallery'][0].src } alt={ props.fishData[fish]['Image Gallery'][0].alt } />}
                        {fish === 6 && <img src={ props.fishData[fish]['Image Gallery'].src } alt={ props.fishData[fish]['Image Gallery'].alt } />}
                    </Link>
                </div>
            ))}
        </div>
    );
}
 
export default RandomCatchItem;